<template>

         <svg width="80" height="80" class="lab">
             <mui-bgr :w="80" :h="80" class="mui-spin-background"/>

             <mui-label :x="2" :y="2" :w="76" :h="10" label="x:y" />
             <mui-label :x="2" :y="14" :w="76" :h="10" :label="value.x+':'+value.y" />
             
             <g ref="circle">
                 <line class="line" :x1="movexby" :y1="0" :x2="movexby" :y2="80"/>
                 <line class="line" :x1="0" :y1="moveyby" :x2="80" :y2="moveyby"/>
                 <circle class="dot" :cx="movexby" :cy="moveyby" r="3"/>
            </g>
            
            <mui-activearea @onUpdate="update" @onFocus="(e)=>{infocus = e}" :w="80" :h="80" :throttle="throttle" />
                <mui-boundbox :w="80" :h="80" />
         </svg>
</template>



<script>
// @ is an alias to /src

import muiLabel from '@/components/mui/base/muiLabel.vue'
import {rangexy} from '@/components/mui/mixins/ranges.js'
import {precision} from '@/components/mui/mixins/precision.js'
import muiActiveArea from '@/components/mui/base/muiActiveArea.vue'
import muiBackground from '@/components/mui/base/muiBackground.vue'
import muiBoundbox from '@/components/mui/base/muiBoundbox.vue'


export default {
    name: 'muiTrackpad',
    mixins:[rangexy,precision],
    components: {
        'mui-label':muiLabel,
        'mui-activearea':muiActiveArea,
        'mui-bgr':muiBackground,
        'mui-boundbox':muiBoundbox,
    },
    props: {
        throttle:Number,
        rangeout:Object,
        start:Object,
        label:String,
        decimals:Number,
        step:Object,
    },

    data(){
        return{
            movexby:0,
            moveyby:0,
            rangein:{x:{min:0,max:80},y:{min:0,max:80}},
            value:{x:0,y:0}
        }
    },
    mounted:function(){

    if(this.start){
        this.value.x = this.fix(this.start.x);
        this.value.y = this.fix(this.start.y);
    }
    else{
        this.value.x = this.fix(0);
        this.value.y = this.fix(0);
    }
        this.movexby = this.rangeoutin('x',  this.value.x);
        this.moveyby = this.rangeoutin('y',  this.rangeout.y.max - this.value.y);
    },
    methods:{
   
     update:function(e){

        this.movexby+=e.x*this.rangeoutin('x',this.step.x);
        this.moveyby+=e.y*this.rangeoutin('y',this.step.y);

        if(this.movexby > this.rangein.x.max){
            this.movexby = this.rangein.x.max;
        }

        if(this.moveyby > this.rangein.y.max){
            this.moveyby = this.rangein.y.max;
        }

        else if(this.movexby < this.rangein.x.min){
            this.movexby = this.rangein.x.min;
        }

        else if(this.moveyby < this.rangein.y.min){
            this.moveyby = this.rangein.y.min;
        }

        this.value.x = this.fix(this.rangeinout('x',this.movexby));
        this.value.y = this.fix(this.rangeout.y.max - this.rangeinout('y',this.moveyby));

        this.$emit('onchange',{x:this.value.x,y:this.value.y});
        }
    }
}
</script>
<style scoped lang="less">


.lab{
    display:inline-block;
    user-select: none;

    cursor:crosshair;
    &:hover{
        cursor:crosshair;
            .dot-sh{
                opacity: .08
            }
        }
        .dot-sh{
            opacity: .01
        }
}

.dot:hover{}

.line{
    stroke:black;
    stroke-width:1;
    opacity: .5;
}
    </style>
