<template>
   <svg width="80" height="80"  :class="{'vertical':vertical}" class="lab">

       
         <mui-bgr :w="80" :h="80"/>

        <mui-label :x="2" :y="2" :w="76" :h="10" :label="label" />
        <mui-label :x="2" :y="14" :w="76" :h="10" :label="value" />

        <g ref="head">
            <line v-if="vertical" x1="0" y1="0" x2="0" y2="80" class="head-line" />
            <line v-if="!vertical" x1="0" y1="0" x2="80" y2="0" class="head-line" />
            <rect x="0" y="0" class="head-rect" width="80" height="80"/>
        </g>
        <mui-activearea @onUpdate="update" @onFocus="(e)=>{infocus = e}" :w="80" :h="80" :throttle="throttle" />
        <mui-boundbox :w="80" :h="80" />
    </svg>
</template>

<script>
// @ is an alias to /src
import {rangex} from '@/components/mui/mixins/ranges.js'
import {precision} from '@/components/mui/mixins/precision.js'
import muiLabel from '@/components/mui/base/muiLabel.vue'
import muiActiveArea from '@/components/mui/base/muiActiveArea.vue'
import muiBackground from '@/components/mui/base/muiBackground.vue'
import muiBoundbox from '@/components/mui/base/muiBoundbox.vue'

export default {

        name: "muiSlide",
        mixins:[rangex,precision],
        components: {
            'mui-label':muiLabel,
            'mui-activearea':muiActiveArea,
            'mui-bgr':muiBackground,
            'mui-boundbox':muiBoundbox,
        },
        props: {
            label: String,
            throttle: Number,
            rangeout: Object,
            start: Number,
            step:Number,
            decimals:Number,
            vertical:Boolean,
        },
        data() {
            return {
              moveby: 0,
              value: 0,
              rangein:{ min: 0, max: 80 }
            };
        },
        mounted: function() {
            if (this.start) {
              this.value = this.fix(this.start);
            }
            else{
                this.value = this.fix(0);
            }
            this.moveby = this.rangeoutin(this.rangeout.max - this.value);
            this.updateHead(this.moveby);
        },
        methods: {
            update: function(e) {
                if(this.vertical){
                    this.moveby += e.x*this.rangeoutin(this.step);
                }
                else {
                    this.moveby += e.y*this.rangeoutin(this.step);
                }
                if (this.moveby > this.rangein.max) {
                    this.moveby = this.rangein.max;
                } else if (this.moveby < this.rangein.min) {
                    this.moveby = this.rangein.min;
                }
                this.value = this.fix(this.rangeout.max - this.rangeinout(this.moveby));
                this.updateHead(this.moveby);
                this.$emit("onchange", this.value);
            },
            updateHead:function(by){
                if(this.vertical){
                    this.$refs.head.setAttribute(
                        "transform",
                        "translate( " + by + ", 0)"
                    );
                }
                else{
                    this.$refs.head.setAttribute(
                        "transform",
                        "translate(0, " + by + ")"
                    );
                }
            }
        }
    };
</script>
<style scoped>

.lab {
  display: inline-block;
  cursor: ns-resize;
  overflow:hidden;

}
.lab.vertical{
     cursor: ew-resize;
 }





.head-rect{
    fill:none;
    stroke:none;
}

.head-line{
    fill:none;
    stroke:black;
    stroke-width:3;
}




</style>
