<template>
    <rect x="0" y="0" :width="w" :height="w" tabindex="10"
            ref="zone"
            class="activearea bounding-box"
            :style="style"
            @focus="()=>focus(true)"
            @blur="()=>focus(false)"
            @mousedown="mousedown"
            @mouseup="mouseup"
            @click="click"
            @keyup.up="onkeyup"
            @keyup.down="onkeydown"

    />
</template>

<script>
export default {
  name: 'muiActiveArea2',
  data(){
      return{
          zone:null,
          lastupdate:0,
        }
  },
  props: {
    throttle:Number,
    w:Number,
    h:Number,
},
computed:{
    style:function(){
        // si this at all needed?
        return { width:this.w+"px",height:this.h+"px"};
    }
},
mounted:function(){    
    this.zone = this.$refs.zone;
},
methods:{
    click:function(e){
        
        if(e.shiftKey){
            this.$emit('onClick',{x:e.offsetX, y:e.offsetY});
        }
    },
    focus:function(e){
        // this.$emit('onFocus',e);
    },
    
    onkeyup:function(e){
        let turbo = 1;
        if (e.shiftKey) {
            turbo = 10;
        }
        if (e.altKey) {
            turbo = .10;
        }
        this.$emit('onKey',{x:-1 * this.throttle * turbo, y:-1 * this.throttle * turbo});
    },
    onkeydown:function(e){
        let turbo = 1;
        if (e.shiftKey) {
            turbo = 10;
        }
        if (e.altKey) {
            turbo = .10;
        }
        this.$emit('onKey',{x:1 * this.throttle * turbo, y:1 * this.throttle * turbo});
    },
    mousedown:function(e){
        window.addEventListener('mousemove',this.mousemove);
        window.addEventListener('mouseup',this.mouseup);
        // this.zone.addEventListener('mouseout',this.mouseout);
    },
    mouseout:function(e){
        console.log('mouseout');
        // this.zone.removeEventListener('mousemove',this.mousemove);
        // this.zone.removeEventListener('mouseout',this.mouseout);
    },
    mouseup:function(e){
        window.removeEventListener('mousemove',this.mousemove);
        window.removeEventListener('mouseup',this.mouseup);
        // this.zone.removeEventListener('mouseout',this.mouseout);
    },
    mousemove:function(e){

        let d = new Date();

        
        
        let turbo = 1;
        if (e.shiftKey) {
            turbo = 10;
        }
        if (e.altKey) {
            turbo = .10;
        }
        
        if(d.getTime() > this.lastupdate + 1){
            this.lastupdate = d.getTime();
            this.$emit('onUpdate',{x:e.movementX * this.throttle * turbo, y:e.movementY * this.throttle * turbo});
        }
    }
}
}
</script>
<style scoped lang="less">
.activearea{
    fill:cyan;
    opacity:0;
    stroke:none;
}
.bounding-box{
    background:yellow;
    &:focus{
        outline:2px dashed aqua;
        z-index:5000;
    }
}
</style>
