import Vue from "vue";

import muiGroup from '@/components/mui/utils/muiGroup.vue';
import muiPad from '@/components/mui/muiPad.vue';
import muiSpin from '@/components/mui/muiSpin.vue';
import muiSlide from '@/components/mui/muiSlide.vue';
import muiTrackpad from '@/components/mui/muiTrackpad.vue';
import muiLabel from '@/components/mui/base/muiLabel.vue'
import muiActiveArea from '@/components/mui/base/muiActiveArea.vue'
import muiBackground from '@/components/mui/base/muiBackground.vue'


const Components = {

    "mui-group":muiGroup,
    "mui-pad":muiPad,
    "mui-spin":muiSpin,
    "mui-slide":muiSlide,
    "mui-trackpad":muiTrackpad,
    "mui-label":"muiLabel",
    "mui-activearea":muiActiveArea,
    "mui-background":muiBackground
}

Object.keys(Components).forEach(name=>{
    Vue.component(name,Components[name]);
})

export default Components;
