<template>
    <svg :width="w" :height="h" ref="mainarea" class="lab">
        <mui-bgr :w="w" :h="h" class="mui-spin-background"/>
        <rect :x="padding" :y="padding" :width="w - padding*2" :height="h - padding*2" @dblclick="dbl" @click="trigger()" class="pad" :class="{is_on:on,flash:flash}"></rect>
        <mui-text v-if="label" :x="2" :y="2" :w="76" :h="10" :label="label" />
         <mui-boundbox :w="w" :h="h" />
        
    </svg>
</template>


<script>

import muiGrid from "@/components/mui/base/muiGrid.vue";
import muiLabel from '@/components/mui/base/muiLabel.vue'
import muiText from '@/components/mui/base/muiText.vue'
import {rangex} from '@/components/mui/mixins/ranges.js'
import muiActiveArea from '@/components/mui/base/muiActiveArea.vue'
import muiBackground from '@/components/mui/base/muiBackground.vue'
import muiBoundbox from '@/components/mui/base/muiBoundbox.vue'

export default {
  name: "muiPad",
  components: {
      'mui-grid':muiGrid,
      'mui-text':muiText,
      'mui-label':muiLabel,
      'mui-activearea':muiActiveArea,
      'mui-bgr':muiBackground,
      'mui-boundbox':muiBoundbox,
  },
  props: {
    is_on: Boolean,
    index: Number,
    w: Number,
    h: Number,
    padding: Number,
    label:String,
    mode:String,
    sustain:Number,
    push:Boolean,
  },


  data() {
    return {
      on: false,
      flash: false,
      element:'',
    };
  },
  mounted: function() {
      this.element=this.$refs.mainarea;
      if('toggle' == this.mode){
        this.on = this.is_on;
      }
      if('hold' == this.mode){
          this.element.addEventListener('mouseup',(e)=>{
              this.on = false;
              this.$emit("update", { id: this.index, is_on: this.on });
          });
      }
  },
  watch:{
      push:function(val){
        
          this.flash = val;
      },
      is_on:function(val){
        this.on=val;
      }
  },

  methods: {

    dbl:function(e){
        // this.flash=1;
       this.$emit("dbl", { id: this.index, is_on: this.on });
    },
    trigger:function(){

         switch (this.mode) {
             case 'toggle':
                this.toggle();
             break;
             case 'shot':
                this.blast();
             break;
             case 'hold':
                this.on = true;
                this.$emit("update", { id: this.index, is_on: this.on });


             break;

         }
    },
    toggle: function() {
      this.on = !this.on;
      this.$emit("update", { id: this.index, is_on: this.on });
    },
    blast: function() {
      this.on = true;
      this.flash = true;
      this.$emit("update", { id: this.index, is_on: this.on });
      let s = this.sustain? this.sustain : 300;
      console.log('shot on')
      setTimeout(() => {
        console.log('shot off')
        this.on = false;
        this.flash = false;
        this.$emit("update", { id: this.index, is_on: this.on });
    }, s);

    }
  }
};
</script>

<style scoped>


.lab{
    display:inline-block;
    user-select: none;
    cursor:pointer;
}

.pad {

  fill:var(--mui-c-off);
  stroke:none;
  transition: fill .5s;

  will-change: fill,stroke;
}
.pad.is_on {

  fill:var(--mui-c-on);
  transition: fill .1s;

}

.pad.flash {
  fill:var(--mui-c-flash);
  transition: fill .1s;
}

</style>
