<template>

    <svg width="80" height="80"  class="lab">
             <mui-bgr :w="80" :h="80"/>

             <mui-label :x="2" :y="2" :w="76" :h="10" :label="label" />
             <mui-label :x="2" :y="14" :w="76" :h="10" :label="value" />

             <g ref="head">
                 <circle class="st0 circle" cx="40" cy="55" r="20"/>
                 <line class="st1 pointer" x1="40" y1="55" x2="40" y2="75"/>
             </g>
             
            <mui-activearea @onUpdate="update" @onFocus="(e)=>{infocus = e}" :w="80" :h="80" :throttle="throttle" />
            <mui-boundbox :w="80" :h="80" />
            

         </svg>


</template>
<script>


// @ is an alias to /src

import muiLabel from '@/components/mui/base/muiLabel.vue'
import {rangex} from '@/components/mui/mixins/ranges.js'
import {precision} from '@/components/mui/mixins/precision.js'
import muiActiveArea from '@/components/mui/base/muiActiveArea.vue'
import muiBackground from '@/components/mui/base/muiBackground.vue'
import muiBoundbox from '@/components/mui/base/muiBoundbox.vue'

export default {

    name: 'muiSpin',
    mixins:[rangex,precision],
    components: {
        'mui-label':muiLabel,
        'mui-activearea':muiActiveArea,
        'mui-bgr':muiBackground,
        'mui-boundbox':muiBoundbox,
    },
    props: {
        msg: String,
        throttle:Number,
        decimals:Number,
        rangeout:Object,
        start:Number,
        label:String,
        step:Number,
        steps:Array || Number,
        stepmode:Boolean,
        push:Number,
    },
    watch:{
        push:function(val){
            this.value = val;
            this.movexby = this.rangeoutin(val);
            this.$refs.head.setAttribute('transform',"rotate( "+this.movexby+",40,55)")
        }
    },
    data(){
        return{
            movexby:0,
            value:0,
            rangein:{min:30,max:330}

        }
    },
    mounted:function(){
        if(this.start){
            this.value = this.fix(this.start);
        }
        else{
            this.value = this.fix(0);
        }

        this.movexby = this.rangeoutin(this.start);
        this.updateHead(this.movexby);
         this.$emit('onchange',this.value);


    },
    methods:{


        update:function(e){


            if(this.stepmode === true){

                this.movexby+=Math.round(e.x);
                if(this.movexby >= this.steps.length){
                    this.movexby = this.steps.length - 1;
                }
                else if(this.movexby < 0){
                    this.movexby = 0;
                }

                this.value = this.steps[Math.round(this.movexby)];


                let r = this.rangein.max-this.rangein.min;
                let sl = this.steps.length - 1;
                let ang = (r/sl);
                let rotateby = ang * (this.movexby);
                this.$refs.circle.setAttribute('transform',"rotate( " + (rotateby + this.rangein.min) +",40,55)");
                this.$emit('onchange',this.value);

                return;

            }



            else{

                this.movexby+=e.x*this.rangeoutin(this.step);///Math.pow(10,this.decimals)/10;

                if(this.movexby > this.rangein.max){
                    this.movexby = this.rangein.max;
                }
                else if(this.movexby < this.rangein.min){
                    this.movexby = this.rangein.min;
                }

                // this.value = Math.round(this.rangeinout(this.movexby));
                this.value = this.fix(this.rangeinout(this.movexby).toFixed(this.decimals));
            }
            this.updateHead(this.movexby);
            this.$emit('onchange',this.value);
        },
        updateHead:function(by){
          this.$refs.head.setAttribute('transform',"rotate( "+by+",40,55)")
        }
    },
}
</script>

<style scoped>

.lab{
    display:inline-block;
    cursor:ew-resize;
    user-select: none;
}

</style>
